/* CONTAINER */
/*======================================*/
#container {
  min-height: 100vh;
  height: auto;
  position: relative;
  min-width: 290px;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: opacity 0.5s linear 1s;
  transition: opacity 0.5s linear 1s;
}
.nifty-ready #container {
  opacity: 1;
  visibility: visible;
  -webkit-transition: opacity 0.5s linear 1.5s;
  transition: opacity 0.5s linear 1.5s;
}
#container.boxed-layout {
  background-color: #c5c8cb;
}
/* CONTENT */
/*======================================*/
#content-container {
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #dbdfe2;
}
/* PAGE HEADER */
/*======================================*/
#page-title {
  padding: 10px 20px;
  width: 100%;
}
.page-header {
  border: 0 none;
  color: #7c7c7c;
  font-size: 2.2em;
  font-weight: 100;
  margin: 0;
  padding: 10px 0;
}
.page-header .label {
  margin-right: 0.5em;
}
/* PAGE CONTENT */
/*======================================*/
#page-content {
  padding: 5px 20px 0;
}
@media (min-width: 768px) {
  #page-content {
    padding: 10px 20px 0;
  }
}
@media (min-width: 992px) {
  #page-content {
    padding: 20px 25px 0;
  }
}
/* SEARCHBOX */
/*======================================*/
.searchbox {
  margin-right: -20px;
}
.searchbox button {
  border: 0;
  background: none;
  padding: 2px 5px;
  margin-top: 2px;
  position: relative;
  left: -28px;
  font-size: 14px;
  z-index: 2;
}
.searchbox .custom-search-form {
  margin-right: -5px;
}
.searchbox .custom-search-form input {
  border-color: transparent;
  padding-right: 30px;
}
.searchbox > .input-group.custom-search-form input {
  border-radius: 0;
}
.searchbox > .input-group input:focus {
  border-color: #2e3539;
}
/* PAGE CONTENT - RESPONSIVE LAYOUT */
/*======================================*/
@media (min-width: 768px) {
  #content-container {
    padding-top: 50px;
  }
  #page-title {
    display: table;
    table-layout: fixed;
    height: 51px;
    padding: 0 0 0 15px;
  }
  .page-header,
  .searchbox {
    display: table-cell;
    vertical-align: middle;
  }
  .searchbox {
    width: 300px;
  }
  .searchbox .form-control {
    background-color: transparent;
    -webkit-transition: background-color 0.5s, border-color 0.5s;
    transition: background-color 0.5s, border-color 0.5s;
  }
  .searchbox .form-control:focus {
    background-color: #fff;
    -webkit-transition: background-color 0.5s, border-color 0.5s;
    transition: background-color 0.5s, border-color 0.5s;
  }
}
@media (min-width: 992px) {
  .searchbox {
    margin-right: -25px;
  }
  .searchbox .custom-search-form {
    margin-right: 0;
  }
  #page-title {
    padding: 10px 0 0 25px;
  }
}
/* CLEAN ZONE (LOGIN, REGISTER, AND ERROR PAGES CONTAINER) */
/*======================================*/
.cls-container {
  background-color: #212124;
  color: #ffffff;
  text-align: center;
}
.cls-container a,
.cls-container a:hover,
.cls-container a:focus {
  color: #ffffff;
}
.cls-container .text-light a,
.cls-container .text-light a:hover,
.cls-container .text-light a:focus {
  color: #fff;
}
/* Image background */
.bg-img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: .55;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
/* Header */
.cls-header {
  position: relative;
  background-color: rgba(0, 0, 0, 0.1);
}
.cls-header:after {
  content: "";
  display: block;
  border-top: 10px solid rgba(0, 0, 0, 0.1);
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  width: 0;
  height: 0;
  position: absolute;
  left: 50%;
  margin-bottom: -10px;
  margin-left: -10px;
  bottom: 0;
}
.cls-header.cls-header-lg .cls-brand .brand-icon {
  width: 60px;
  height: 60px;
}
#container.cls-container .cls-header .cls-header.cls-header-lg .cls-brand .brand-title {
  font-size: 27px;
}
/* Brand */
.cls-brand {
  display: inline-block;
  padding: 20px 0;
}
.cls-brand .brand-icon {
  padding: 0;
}
.cls-brand a:hover .brand-icon,
.cls-brand a:hover .brand-title {
  opacity: 1;
  color: #fff;
  transition: all 0.5s;
}
.cls-brand a .brand-icon,
.cls-brand a .brand-title {
  opacity: .7;
  transition: all 0.5s;
}
.cls-header-lg .cls-brand {
  line-height: 60px;
}
.cls-header-sm .cls-brand {
  line-height: 1.42857;
}
#container.cls-container .cls-brand .brand-icon,
#container.cls-container .cls-brand .brand-title {
  background-color: transparent;
  color: inherit;
  float: none;
  display: inline-block;
  line-height: 22px;
  font-size: 20px;
  font-weight: 600;
  padding: 5px;
  vertical-align: middle;
  width: auto;
}
/* Content */
.cls-content {
  padding: 50px 15px 15px;
  padding-top: 10vh;
  position: relative;
}
.cls-content .error-code {
  font-size: 100px;
  font-weight: 300;
}
.cls-content .error-search {
  max-width: 512px;
  margin: 0 auto;
}
.cls-content .cls-content-sm,
.cls-content .cls-content-lg {
