/* TIMELINE */
/*======================================*/
.timeline {
  position: relative;
  padding-bottom: 40px;
  background-color: #dbdfe2;
  color: #515151;
}
.timeline:before,
.timeline:after {
  background-color: #cad3df;
  bottom: 20px;
  content: "";
  display: block;
  position: absolute;
}
.timeline:before {
  left: 49px;
  top: 20px;
  width: 2px;
}
.timeline:after {
  left: 47px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
}
.timeline-header {
  border-radius: 0;
  clear: both;
  margin-bottom: 50px;
  margin-top: 50px;
  position: relative;
}
.timeline-header .timeline-header-title {
  display: inline-block;
  text-align: center;
  padding: 7px 15px;
  min-width: 100px;
}
.timeline .timeline-header:first-child {
  margin-bottom: 30px;
  margin-top: 15px;
}
.timeline-stat {
  width: 100px;
  float: left;
  text-align: center;
  padding-bottom: 15px;
}
.timeline-entry {
  margin-bottom: 50px;
  margin-top: 5px;
  position: relative;
  clear: both;
}
.timeline-entry-inner {
  position: relative;
}
.timeline-time {
  display: inline-block;
  padding: 5px 3px 7px;
  margin-top: 3px;
  background-color: #dbdfe2;
  color: #848484;
  font-size: .85em;
  max-width: 70px;
}
.timeline-icon {
  border-radius: 50%;
  box-shadow: 0 0 0 7px #dbdfe2;
  display: block;
  margin: 0 auto;
  height: 46px;
  line-height: 46px;
  text-align: center;
  width: 46px;
}
.timeline-icon img {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  vertical-align: top;
}
.timeline-icon:empty {
  height: 10px;
  width: 10px;
  margin-top: 20px;
  background-color: #a4b4c7;
}
.timeline-label {
  background-color: #ffffff;
  border-radius: 0;
  margin-left: 90px;
  padding: 10px;
  position: relative;
  min-height: 50px;
  border: 1px solid #e9e9e9;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.05);
}
.timeline-label:before,
.timeline-label:after {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  left: 0;
  top: 0;
}
.timeline-label:before {
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 10px solid #e6e6e6;
  margin: 15px 0 0 -10px;
}
.timeline-label:after {
  border-top: 9px solid transparent;
  border-bottom: 9px solid transparent;
  border-right: 9px solid #ffffff;
  margin: 15px 0 0 -8px;
}
.panel .timeline,
.panel .timeline-time {
  background-color: #fff;
}
.panel .timeline-icon {
  box-shadow: 0 0 0 7px #fff;
}
.panel .timeline-label {
  box-shadow: none;
  background-color: #f7f7f7;
  border: 1px solid #e3e3e3;
}
.panel .timeline-label:before {
  border-right-color: #e3e3e3;
}
.panel .timeline-label:after {
  border-right-color: #f7f7f7;
}
@media (min-width: 768px) {
  .two-column.timeline {
    text-align: center;
  }
  .two-column.timeline:before {
    left: 50%;
  }
  .two-column.timeline:after {
    left: 50%;
    margin-left: -2px;
  }
  .two-column.timeline .timeline-entry {
    width: 50%;
    text-align: left;
  }
  .two-column.timeline .timeline-stat {
    margin-left: -50px;
  }
  .two-column.timeline .timeline-entry:nth-child(odd) {
    float: right;
  }
  .two-column.timeline .timeline-entry:nth-child(odd) .timeline-label {
    margin-left: 40px;
  }
  .two-column.timeline .timeline-header {
    text-align: center;
  }
  .two-column.timeline .timeline-entry:nth-child(even) {
    float: left;
  }
  .two-column.timeline .timeline-entry:nth-child(even) .timeline-stat {
    left: 100%;
    position: relative;
    margin-left: -50px;
  }
  .two-column.timeline .timeline-entry:nth-child(even) .timeline-label {
    left: -90px;
    margin-right: -40px;
  }
  .two-column.timeline .timeline-entry:nth-child(even) .timeline-label:before,
  .two-column.timeline .timeline-entry:nth-child(even) .timeline-label:after {
    left: auto;
