  padding: 10px 15px;
  cursor: pointer;
}
/* FORM WIZARD */
/*======================================*/
.wz-nav-off > li a {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  cursor: default !important;
}
.wz-icon-inline li > a .icon-wrap {
  display: inline-block;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  color: inherit;
}
.wz-icon-bw li > a .icon-wrap,
.wz-icon-bw li > a p {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.wz-icon-bw li.active ~ li > a .icon-wrap {
  color: #515151;
  background-color: rgba(0, 0, 0, 0.17);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.wz-icon-bw li:not(.active) > a p {
  color: #515151 !important;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
/* FORM WIZARD : Classic */
/*======================================*/
.wz-classic {
  margin: 0;
  padding: 0;
  list-style: none;
  display: block;
  position: relative;
}
.wz-classic li,
.wz-steps li {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.wz-classic li > a {
  color: inherit;
  display: block;
  text-align: center;
  padding: 20px 0;
}
.wz-classic li > a .icon-wrap {
  display: inline-block;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  color: inherit;
}
.wz-classic:after,
.wz-steps:after {
  content: '';
  display: table;
  clear: both;
}
.wz-classic .active ~ li {
  color: inherit;
  background-color: inherit;
}
.wz-classic .active ~ li a {
  opacity: .5;
}
/* FORM WIZARD : Steps */
/*======================================*/
.wz-heading {
  position: relative;
}
.wz-heading .progress {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  background-color: transparent;
}
.wz-heading.wz-w-label .progress {
  margin-top: -0.5em;
}
.wz-steps {
  margin: 0;
  padding: 20px 0;
  list-style: none;
  display: block;
  position: relative;
}
.wz-steps li > a {
  color: inherit;
  display: block;
  text-align: center;
  padding: 0;
}
.wz-steps li > a .icon-wrap {
  display: inline-block;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
}
.wz-steps li .wz-desc {
  opacity: 0;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.wz-steps li:not(.active) .wz-icon {
  display: none;
}
.wz-steps .active ~ li {
  color: inherit;
}
.wz-steps .active ~ li a {
  opacity: .5;
}
.wz-steps .active ~ li .wz-icon {
  display: inline-block;
}
.wz-steps .active .wz-icon-done,
.wz-steps .active ~ li .wz-icon-done {
  display: none;
}
.wz-steps .active .wz-desc {
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
/* SCROLL TO TOP BUTTON */
/*======================================*/
#scroll-top {
  background-color: #404449;
  color: #fff;
  cursor: pointer;
  position: fixed;
  bottom: 55px;
  font-size: 20px;
  right: 5px;
  border-radius: 2px;
  opacity: 0;
  z-index: 999;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
#scroll-top.in {
  opacity: .5;
  transition: opacity .3s;
}
#scroll-top.in:hover {
  opacity: 1;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
/* NIFTY OVERLAY */
/*======================================*/
.panel-overlay-wrap {
  position: relative;
}
.panel-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.75);
  text-align: center;
  z-index: 795;
}
.panel-overlay:before {
  content: "";
  display: inline-block;
  height: 100%;
  width: 1px;
  vertical-align: middle;
  margin-left: -5px;
}
.panel-overlay-title {
  margin: 10px 0 5px;
}
.panel-overlay-icon {
  display: inline-block;
  vertical-align: middle;
}
.panel-overlay-content {
  display: inline-block;
  vertical-align: middle;
}
/* NIFTY LANGUAGE SELECTOR */
/*======================================*/
.lang-flag {
  display: inline-block;
  padding: 3px 5px 3px 0;
}
.lang-id {
  font-weight: 600;
}
.lang-id,
.lang-name {
  display: none;
  vertical-align: middle;
}
.lang-selector .lang-id {
  display: inline-block;
}
.lang-selector + .dropdown-menu .lang-name {
  display: inline-block;
}
.lang-selector + .dropdown-menu.with-arrow {
  margin-top: 10px;
}
.lang-selector + .dropdown-menu a {
  padding: 10px;
}
.lang-selector + .dropdown-menu .active {
  display: none;
}
@media (min-width: 992px) {
  .lang-id,
  .lang-selector .lang-id {
    display: none;
  }
  .lang-name {
    display: inline-block;
  }
}
/* NIFTY NOTIFICATION */
/*======================================*/
.alert-wrap {
  margin: 0;
  max-height: 0;
  overflow: hidden;
  padding: 0;
  -webkit-transition: max-height 0.7s linear;
  transition: max-height 0.7s linear;
}
.alert-wrap > .alert {
  margin: 0;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  border-radius: 0;
  text-align: left;
}
.alert-wrap > .alert > .media {
  margin: 0;
}
.alert-wrap > .alert > .media > .media-body {
  min-width: 150px;
  width: auto;
  vertical-align: middle;
}
#page-alert > .alert-wrap > .alert {
  box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.04);
}
.alert-wrap.in {
  max-height: 500px;
  -webkit-transition: max-height 1s linear;
  transition: max-height 1s linear;
}
.floating-container .animated.alert-wrap {
  overflow: visible;
}
.floating-container .animated.alert-wrap > .alert {
  border-radius: 2px;
}
.alert-message,
.alert-title {
  font-size: .9em;
  margin-bottom: 1px;
  padding-right: 25px;
}
.alert-title {
  font-size: 1.12em;
}
.alert-title:empty {
  display: none;
}
.alert-primary .alert-icon {
  background-color: #9bc5ea;
  color: #ffffff;
}
.alert-info .alert-icon {
  background-color: #91d5e8;
  color: #ffffff;
}
.alert-success .alert-icon {
  background-color: #c1dda3;
  color: #ffffff;
}
.alert-warning .alert-icon {
  background-color: #f3ca8f;
  color: #ffffff;
}
.alert-danger .alert-icon {
  background-color: #faa393;
  color: #ffffff;
}
.alert-mint .alert-icon {
  background-color: #92dcc8;
  color: #ffffff;
}
.alert-purple .alert-icon {
  background-color: #bf9dba;
  color: #ffffff;
}
.alert-pink .alert-icon {
  background-color: #ecadc8;
  color: #ffffff;
}
.alert-dark .alert-icon {
  background-color: #85898c;
  color: #ffffff;
}
/* Floating */
/*======================================*/
#floating-top-right {
  position: fixed;
  text-align: right;
  top: 7px;
  right: 7px;
  left: 7px;
  z-index: 15;
}
#floating-top-right .alert-wrap {
  display: inline-block;
  clear: right;
  float: right;
  margin-bottom: 5px;
  position: relative;
  opacity: .96;
  z-index: 990;
}
#floating-top-right .alert-wrap:hover {
  opacity: 1;
}
@media (min-width: 768px) {
  #floating-top-right {
    left: auto;
    max-width: 727px;
  }
}
@media (min-width: 992px) {
  #floating-top-right {
    left: auto;
    max-width: 50%;
  }
}
@media (min-width: 1200px) {
  #floating-top-right {
    left: auto;
    max-width: 35%;
  }
}
